/* Loader.css */
@import 'variables.scss';
.routing-loader-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 9999;

    .spinner {
      width: 50px;
      height: 50px;
      border: 5px solid $primary-color; /* orange */
      border-top: 5px solid transparent;
      border-radius: 50%;
      animation: spin 0.8s linear infinite;
    }
  }
  

  
  @keyframes spin {
    to {
      transform: rotate(360deg);
    }
  }
  